const form = require('@/components/common/tui-validation/tui-validation.js');
var { parseTime } = require('wx-tool');
import { mapState } from 'vuex';

export default {
	data() {
		return {
			isUrgent: false,
			message: undefined
		};
	},
	computed: mapState(['nickName', 'avatarUrl']),
	methods: {
		formSubmit: function(e) {
			//表单规则
			let rules = [
				{
					name: 'message',
					rule: ['required', 'maxLength:500'],
					msg: ['请输入留言', '留言不能超过500个字']
				}
			];
			//进行表单检查
			let formData = e.detail.value;
			let checkRes = form.validation(formData, rules);
			if (!checkRes) {
				let db = wx.cloud.database({
					env: 'jnmc-ronxp'
				});

				db.collection('message').add({
					data: {
						message: this.message,
						is_urgent: this.isUrgent,
						create_time: parseTime(Date.parse(new Date()), '{y}-{m}-{d} {h}:{i}:{s}'),
						username: this.nickName,
						avatar_url: this.avatarUrl
					},
					success: res => {
						uni.showToast({
							title: '留言成功',
							icon: 'success',
							success: function() {
								setTimeout(function() {
									uni.switchTab({
										url: './index',
										success: function(e) {
											const page = getCurrentPages().pop();
											if (!page) return;
											page.onLoad();
										}
									});
								}, 1000);
							}
						});
					},
					fail: err => {
						console.log('留言失败', err);
					}
				});
			} else {
				uni.showToast({
					title: checkRes,
					icon: 'none'
				});
			}
		},
		handleSwitchUrgent(e) {
			this.isUrgent = e.detail.value;
		}
	}
};
